
body{

  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background-color: black;
}
*{
  padding: 0;
  margin: 0;
}
#heroText{
  font-family: intro;
  font-size: 100px;
  position: absolute;
  color: white;
  padding: 0;
  margin: 0;
  justify-content: center;
  
}

.secStart{
  justify-content: center;
  justify-self: center;
  display: flex;
}
canvas{
top: 0;
left: 0;
padding: 0;
margin: 0;
position: fixed;

}

@font-face {
  font-family: intro;
  src: url(./font/Candu.ttf);
}
.album1{
  height: 1000px;
  width: 1500px;
  background-repeat: no-repeat;
  filter: blur(80px);
  top: 800px;
  padding: 0;
  margin: 0;
  opacity: 0;
  position: absolute;
}